html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after {
  content: '';
  content: none;
}

q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* This used to work for the parent element of button divs */

/* But it does not work with newer browsers, the below doesn't hide the play button parent div */

*::-webkit-media-controls-panel {
  display: none !important;
  -webkit-appearance: none;
}

/* Old shadow dom for play button */

*::-webkit-media-controls-play-button {
  display: none !important;
  -webkit-appearance: none;
}

/* New shadow dom for play button */

/* This one works! */

*::-webkit-media-controls-start-playback-button {
  display: none !important;
  -webkit-appearance: none;
}

video {
  object-fit: cover;
}

html,
body {
  padding: 0;
  margin: 0;
  min-width: 100%;
  min-height: 100%;
  background-color: #F9F8F6;
  -webkit-text-size-adjust: 100%;
}

html,
body,
a {
  font-family: Helvetica, Arial, sans-serif;
  color: #3D3D3D;
  letter-spacing: 0.42px;
}

body {
  position: relative;
}

input::placeholder {
  font-family: Helvetica, Arial, sans-serif;
  color: #3D3D3D;
  letter-spacing: 0.42px;
}

div,
section,
form,
label {
  display: flex;
  width: auto;
  height: auto;
  position: relative;
  box-sizing: border-box;
  flex-direction: column;
  flex-shrink: 0;
}

* {
  text-decoration: none;
  font-weight: unset;
}

*:focus {
  outline: none;
}

a:link,
a:visited,
a:hover,
a:active {
  color: unset;
}

.side-nav-page {
  width: 100%;
  flex-direction: row;
}

.side-nav-page .page-content {
  flex: 1;
  margin-left: 170px;
}

.ports-header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

.ports-desktop-header {
  width: 100%;
  flex-direction: row;
  align-items: center;
  padding-top: 23px;
  padding-bottom: 23px;
  transition: background-color 0.35s;
  transition-delay: 0.2s;
}

.ports-desktop-header *,
.ports-desktop-header a,
.ports-desktop-header a:hover,
.ports-desktop-header a:visited,
.ports-desktop-header a:active {
  color: #F9F8F6;
}

.ports-desktop-header a,
.ports-desktop-header .title {
  text-transform: uppercase;
  font-size: 10px;
  line-height: 15px;
  margin-right: 30px;
  cursor: pointer;
}

.ports-desktop-header .nav-left a,
.ports-desktop-header .nav-right a {
  border-bottom: 2px solid transparent;
  margin-right: 40px;
  transition: border-color 0.15s, color 0.05s;
  font-weight: bold;
}

.ports-desktop-header .nav-left a.show-underline,
.ports-desktop-header .nav-left a:hover,
.ports-desktop-header .nav-right a.show-underline,
.ports-desktop-header .nav-right a:hover {
  border-color: #3D3D3D;
}

.ports-desktop-header a:last-child {
  margin-right: 0;
}

.ports-desktop-header .nav-left {
  width: 33.3%;
  flex-direction: row;
}

.ports-desktop-header .logo-container {
  width: 33.3%;
  align-items: center;
  height: 0;
}

.ports-desktop-header .logo-container a:hover,
.ports-desktop-header .logo-container a:visited,
.ports-desktop-header .logo-container a:active {
  opacity: 1;
}

.ports-desktop-header .logo-container .logo {
  width: 61px;
  height: 31px;
  transform: translateY(-53%);
}

.ports-desktop-header .nav-right {
  width: 33.3%;
  flex-direction: row;
  justify-content: flex-end;
}

.ports-desktop-header .nav-right a:last-child {
  margin-right: 0;
}

.ports-desktop-header .nav-level-2 {
  position: absolute;
  left: 0;
  width: 100%;
  background-color: rgba(249, 248, 246, 0.94);
  border-left: none;
  border-right: none;
  flex-direction: row;
  transition: height 0.35s;
  overflow: hidden;
}

.ports-desktop-header .nav-level-2 .collection {
  margin-right: 48px;
  transition: opacity 0.2s;
  transition-delay: 0s;
}

.ports-desktop-header .nav-level-2 .collection .title {
  font-weight: bold;
  margin-right: 0;
  padding: 23px 0 15px 0;
}

.ports-desktop-header .nav-level-2 .collection .links .link {
  margin-bottom: 6px;
  text-transform: capitalize;
}

.ports-desktop-header .nav-level-2 .collection .links .link:hover {
  font-weight: bold;
}

.ports-desktop-header .nav-level-2 .collection .links.visible {
  opacity: 1;
  transition-delay: 0.1s;
}

.ports-desktop-header .nav-level-2.visible .collection {
  opacity: 1;
  transition-delay: 0.1s;
}

.ports-desktop-header .search-subnav {
  position: absolute;
  left: 0;
  width: 100%;
  background-color: rgba(249, 248, 246, 0.94);
  overflow: hidden;
  transition: height 0.35s;
  align-items: center;
}

.ports-desktop-header.dark *,
.ports-desktop-header.dark a,
.ports-desktop-header.dark a:hover,
.ports-desktop-header.dark a:visited,
.ports-desktop-header.dark a:active {
  color: #3D3D3D;
}

.ports-desktop-header:hover {
  background-color: #F9F8F6;
  transition-delay: 0s;
}

.ports-desktop-header:hover *,
.ports-desktop-header:hover a,
.ports-desktop-header:hover a:hover,
.ports-desktop-header:hover a:visited,
.ports-desktop-header:hover a:active {
  color: #3D3D3D;
}

.ports-mobile-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.ports-mobile-header .menu-bar {
  pointer-events: auto;
  flex-direction: row;
  width: 100%;
  align-items: center;
  padding-top: 23px;
  padding-bottom: 23px;
}

.ports-mobile-header .menu-bar .menu-left {
  width: 33%;
  justify-content: center;
}

.ports-mobile-header .menu-bar .menu-left .menu-icon-container {
  cursor: pointer;
}

.ports-mobile-header .menu-bar .menu-left .menu-icon-container .mobile-menu-icon {
  width: 12px;
  height: 12px;
}

.ports-mobile-header .menu-bar .menu-center {
  width: 33%;
  justify-content: center;
  align-items: center;
  height: 0;
}

.ports-mobile-header .menu-bar .menu-center .logo {
  width: 61px;
  height: 31px;
}

.ports-mobile-header .menu-bar .menu-right {
  width: 33%;
  justify-content: flex-end;
  flex-direction: row;
  transform: translateX(4px);
}

.ports-mobile-header .menu-bar .menu-right .mobile-bag-icon {
  width: 14px;
  height: 14px;
}

.ports-mobile-header .mobile-menu {
  width: 100%;
  height: 100%;
  pointer-events: auto;
  position: fixed;
  top: 0;
  left: 0;
  background: #F9F8F6;
  overflow-y: auto;
  overflow-x: hidden;
}

.ports-mobile-header .mobile-menu .controls-container {
  width: 100%;
  height: 73px;
  flex-direction: row;
  justify-content: space-between;
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 23px;
  padding-bottom: 23px;
}

.ports-mobile-header .mobile-menu .controls-container .logo {
  width: 61px;
  height: 31px;
}

.ports-mobile-header .mobile-menu .controls-container .close-icon-container .close-btn {
  cursor: pointer;
  width: 12px;
  height: 12px;
}

.ports-mobile-header .mobile-menu .links-container {
  padding-top: 10px;
  width: 100%;
  flex-direction: row;
  transition: transform 320ms;
  transform: translateX(0);
  position: absolute;
  padding-top: 98px;
  min-height: 100%;
}

.ports-mobile-header .mobile-menu .links-container a,
.ports-mobile-header .mobile-menu .links-container h4 {
  color: #3D3D3D;
  font-size: 12px;
  letter-spacing: 0.6px;
  line-height: 18px;
  margin-bottom: 20px;
}

.ports-mobile-header .mobile-menu .links-container a {
  cursor: pointer;
  position: relative;
}

.ports-mobile-header .mobile-menu .links-container .back-btn {
  font-weight: bold;
}

.ports-mobile-header .mobile-menu .links-container .links-level-1 {
  width: 100%;
  padding-bottom: 23px;
  font-weight: bold;
}

.ports-mobile-header .mobile-menu .links-container .links-level-1 a {
  text-transform: uppercase;
}

.ports-mobile-header .mobile-menu .links-container .links-level-1 .v-shim {
  flex: 1;
}

.ports-mobile-header .mobile-menu .links-container .links-level-1 .account-btn {
  margin-bottom: 0;
}

.ports-mobile-header .mobile-menu .links-container .links-level-2 {
  width: 100%;
}

.ports-mobile-header .mobile-menu .links-container .links-level-2 h4 {
  text-transform: uppercase;
  font-weight: bold;
}

.ports-mobile-header .mobile-menu .links-container .links-level-2 .collections-list {
  padding-left: 22px;
}

.ports-mobile-header .mobile-menu .links-container .links-level-2.shopify-collections a {
  text-transform: uppercase;
}

.ports-mobile-header .mobile-menu .links-container .links-level-3 {
  width: 100%;
}

.ports-mobile-header .mobile-menu .links-container .links-level-3 h4 {
  text-transform: uppercase;
  font-weight: bold;
}

.ports-mobile-header .mobile-menu .links-container .links-level-3 .collections-list {
  padding-left: 22px;
}

.ports-mobile-header.show-level-2 .mobile-menu .links-container {
  transform: translateX(-100%);
}

.ports-mobile-header.show-level-3 .mobile-menu .links-container {
  transform: translateX(-200%);
}

.ports-mobile-header:not(.dark) .menu-bar .logo .border {
  background-color: #F9F8F6 !important;
}

.ports-mobile-header:not(.dark) .menu-bar .logo .logo-circle {
  border-color: #F9F8F6 !important;
}

.ports-footer {
  flex-direction: row;
  width: 100%;
  align-self: center;
  align-items: center;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 0;
  pointer-events: none;
  height: 65px;
  background-color: #f9f8f6;
}

.ports-footer *,
.ports-footer a,
.ports-footer a:hover,
.ports-footer a:visited,
.ports-footer a:active {
  color: #333333;
  font-size: 10px;
  line-height: 15px;
  pointer-events: auto;
}

.ports-footer a:not(.social-icon) {
  justify-content: center;
  align-items: center;
  position: relative;
}

.ports-footer a:not(.social-icon) .bold {
  font-weight: bold;
  pointer-events: none;
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  transform: translateX(-1.4px);
}

.ports-footer a:not(.social-icon):hover div:not(.bold) {
  opacity: 0;
}

.ports-footer a:not(.social-icon):hover div.bold {
  display: flex;
}

.ports-footer a:not(.social-icon).subscribe-btn {
  transform: translateY(2px);
}

.ports-footer > .copyright {
  margin-right: 25px;
}

.ports-footer > .links {
  flex-direction: row;
  margin-right: 25px;
}

.ports-footer > .links a:not(:last-child) {
  margin-right: 25px;
}

.ports-footer > .social {
  flex-direction: row;
  flex: 1;
  justify-content: flex-end;
}

.ports-footer > .social .css-image {
  background-size: auto;
}

.ports-footer > .social a:not(.subscribe-btn) {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  margin-left: 12px;
}

.ports-footer > .social a:first-child {
  margin-left: 0;
}

.ports-footer > .social a.subscribe-btn,
.ports-footer > .social a.subscribe-btn:hover,
.ports-footer > .social a.subscribe-btn:visited,
.ports-footer > .social a.subscribe-btn:active {
  margin-left: 12px;
  color: #333333;
  cursor: pointer;
}

.ports-footer.dark {
  background-color: transparent;
}

.ports-footer.dark *,
.ports-footer.dark a,
.ports-footer.dark a:hover,
.ports-footer.dark a:visited,
.ports-footer.dark a:active {
  color: #3D3D3D;
}

.ports-footer.dark > .social a.subscribe-btn,
.ports-footer.dark > .social a.subscribe-btn:hover,
.ports-footer.dark > .social a.subscribe-btn:visited,
.ports-footer.dark > .social a.subscribe-btn:active {
  color: #3D3D3D;
}

.ports-asset {
  width: 100%;
  height: 100%;
}

.ports-asset .asset-container {
  width: 100%;
  height: 100%;
}

.ports-asset .asset-container .neutral-asset,
.ports-asset .asset-container .hover-asset {
  width: 100%;
  height: 100%;
}

.ports-asset .asset-container .neutral-asset {
  opacity: 1;
  pointer-events: auto;
}

.ports-asset .asset-container .hover-asset {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}

.ports-asset.animate .asset-container {
  opacity: 0;
  transition: opacity 1s;
}

.ports-asset:hover .asset-container .neutral-asset {
  opacity: 0;
  pointer-events: none;
}

.ports-asset:hover .asset-container .hover-asset {
  opacity: 1;
  pointer-events: auto;
}

.ports-asset.loaded .asset-container {
  opacity: 1;
}

.ports-asset.force-hover-asset .asset-container .neutral-asset {
  opacity: 0;
  pointer-events: none;
}

.ports-asset.force-hover-asset .asset-container .hover-asset {
  opacity: 1;
  pointer-events: auto;
}

.ports-asset.is-mobile:hover .asset-container .neutral-asset,
.ports-asset.no-hover-asset:hover .asset-container .neutral-asset {
  opacity: 1;
  pointer-events: auto;
}

.ports-asset.is-mobile:hover .asset-container .hover-asset,
.ports-asset.no-hover-asset:hover .asset-container .hover-asset {
  opacity: 0;
  pointer-events: none;
}

.ports-image {
  width: 100%;
  height: 100%;
}

.ports-image img {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  pointer-events: none;
}

.ports-image div {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.ports-search-input {
  position: relative;
  width: 100%;
}

.ports-search-input input {
  border: none;
  background: none;
  border-bottom: 1px solid #3D3D3D;
  padding-bottom: 15px;
  font-size: 10px;
}

.ports-search-input input::placeholder {
  text-transform: uppercase;
}

.ports-search-input .clear-search-btn {
  width: 12px;
  height: 12px;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
}

.ports-search-input .search-info {
  margin-top: 15px;
  font-size: 10px;
}

.ports-search-input .search-info .searching {
  opacity: 0;
}

.ports-search-input .search-info .recommended-terms {
  flex-direction: row;
}

.ports-search-input .search-info .recommended-terms .term {
  margin-right: 25px;
}

.ports-side-bar {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
  pointer-events: none;
  overflow-x: hidden;
}

.ports-side-bar .side-bar-background {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0;
  transition: opacity 0.2s;
}

.ports-side-bar .side-bar-children {
  padding-left: 40px;
  padding-right: 35px;
  padding-bottom: 30px;
  background: #F9F8F6;
  position: absolute;
  right: 0;
  top: 0;
  min-height: 100%;
  width: 373px;
  transform: translateX(100%);
  transition: transform 0.2s;
}

.ports-side-bar .side-bar-children .close-side-bar-btn {
  position: absolute;
  right: 15px;
  top: 18px;
  width: 12px;
  height: 12px;
  cursor: pointer;
}

.ports-side-bar.visible {
  pointer-events: auto;
}

.ports-side-bar.visible .side-bar-background {
  opacity: 0.2;
}

.ports-side-bar.visible .side-bar-children {
  transform: translateX(0);
}

.ports-subscribe-modal {
  border: 4px solid #3D3D3D;
  background-color: #F9F8F6;
  width: 500px;
  padding: 47px;
  overflow-y: auto;
}

.ports-subscribe-modal a,
.ports-subscribe-modal a:active,
.ports-subscribe-modal a:visited {
  text-decoration: underline;
}

.ports-subscribe-modal a:hover {
  text-decoration: none;
}

.ports-subscribe-modal .hide-btn {
  cursor: pointer;
  position: absolute;
  top: 12px;
  right: 12px;
  width: 12px;
  height: 12px;
}

.ports-subscribe-modal .thank-you {
  pointer-events: none;
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: #3D3D3D;
  font-size: 12px;
  letter-spacing: 0.6px;
  line-height: 20px;
}

.ports-subscribe-modal .page-border {
  display: none;
}

.ports-subscribe-modal .page-margin {
  display: none;
  border-color: #F9F8F6;
}

.ports-subscribe-modal .subscribe-form {
  width: 100%;
  max-width: 375px;
}

.ports-subscribe-modal .subscribe-form h3 {
  color: #3D3D3D;
  font-size: 12px;
  letter-spacing: 0.6px;
  line-height: 20px;
  margin-bottom: 35px;
}

.ports-subscribe-modal .subscribe-form h4 {
  color: #3D3D3D;
  font-size: 10px;
  line-height: 15px;
  margin-bottom: 30px;
}

.ports-subscribe-modal .subscribe-form label[for='email'] {
  color: #3D3D3D;
  font-size: 10px;
  line-height: 15px;
  text-transform: uppercase;
}

.ports-subscribe-modal .subscribe-form input[name='email'] {
  padding: 10px 0;
  margin: 0;
  box-sizing: border-box;
  background: none;
  border: none;
  width: 100%;
  color: #3D3D3D;
}

.ports-subscribe-modal .subscribe-form .email-underline {
  width: 100%;
  height: 1px;
  background-color: #3D3D3D;
  margin-bottom: 25px;
}

.ports-subscribe-modal .subscribe-form .submit-container {
  pointer-events: none;
}

.ports-subscribe-modal .subscribe-form .submit-container .submit {
  text-transform: uppercase;
  display: flex;
  color: #9B9B9A;
  font-size: 10px;
  line-height: 13px;
  cursor: pointer;
  pointer-events: none;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background: none;
  border: none;
  text-decoration: underline;
}

.ports-subscribe-modal .subscribe-form .submit-container input[type='submit'] {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  cursor: pointer;
}

.ports-subscribe-modal .subscribe-form .check-container {
  flex-direction: row;
  margin-bottom: 21px;
}

.ports-subscribe-modal .subscribe-form .check-container .checkbox {
  margin-right: 15px;
  margin-top: 6px;
}

.ports-subscribe-modal .subscribe-form .check-container label {
  color: #3D3D3D;
  font-size: 10px;
  letter-spacing: -0.14px;
  line-height: 16px;
  flex: 1;
}

.ports-subscribe-modal.is-validated .subscribe-form .submit-container {
  pointer-events: auto;
}

.ports-subscribe-modal.is-validated .subscribe-form .submit-container .submit {
  color: #3D3D3D;
}

.ports-subscribe-modal.is-submitted .subscribe-form {
  opacity: 0;
}

.ports-subscribe-modal.is-submitted .thank-you {
  opacity: 1;
  pointer-events: auto;
}

.ports-subscribe-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  z-index: 3;
}

.ports-subscribe-overlay .background {
  background-color: #f9f8f6;
  opacity: 0.9;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.ports-desktop-header {
  padding: 23px 14%;
  transition: background-color 0.35s;
  transition-delay: 0.5s;
}

.ports-desktop-header:hover .ports-asset .asset-container .neutral-asset {
  opacity: 0;
  pointer-events: none;
}

.ports-desktop-header:hover .ports-asset .asset-container .hover-asset {
  opacity: 1;
  pointer-events: auto;
}

.ports-desktop-header.dark .ports-asset .asset-container .neutral-asset {
  display: none;
}

.ports-desktop-header.dark .ports-asset .asset-container .hover-asset {
  opacity: 1;
  pointer-events: auto;
}

.ports-desktop-header .nav-level-2 {
  top: 100%;
  padding-left: 14%;
  padding-right: 14%;
  max-height: 0;
  overflow: hidden;
  transition: 0.5s ease-out;
}

.ports-desktop-header .nav-level-2__content {
  width: 100%;
  flex-direction: row;
}

.ports-desktop-header .nav-level-2 .collection {
  padding-bottom: 8px;
}

.ports-desktop-header .nav-level-2 .collection .title:hover + .links {
  opacity: 1;
  max-height: 150px;
}

.ports-desktop-header .nav-level-2 .collection .title + .links:hover {
  opacity: 1;
  max-height: 150px;
}

.ports-desktop-header .nav-level-2 .collection .links {
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: opacity 0.2s ease, max-height 0.5s ease-out;
}

.ports-desktop-header .nav-level-2 .collection .links .link {
  min-width: 70px;
}

.ports-desktop-header .nav-level-2 .collection .links .link:last-child {
  margin-bottom: 15px;
}

.ports-desktop-header.show-level-2 .nav-level-2 {
  max-height: 150px;
}

.ports-desktop-header .search-subnav {
  top: 100%;
  padding: 0 14%;
  max-height: 0;
  overflow: hidden;
  transition: 0.5s ease-out;
}

.ports-desktop-header .search-subnav .ports-search-input {
  max-width: 60%;
  position: relative;
  margin: 100px auto;
}

.ports-desktop-header.show-search .search-subnav {
  max-height: 300px;
}

.ports-mobile-header {
  display: none;
}

.ports-mobile-header .menu-bar {
  padding: 23px 30px;
}

.ports-mobile-header .controls-container {
  padding: 0 30px;
}

.ports-mobile-header .mobile-menu {
  transform: translateX(-100%);
  transition: 300ms ease;
  padding: 0 30px;
}

.ports-mobile-header.show-menu .mobile-menu {
  transform: translateX(0%);
}

.ports-footer {
  padding: 23px 14%;
}

.ports-subscribe-overlay {
  visibility: hidden;
  opacity: 0;
}

.ports-subscribe-overlay.visible {
  visibility: visible;
  opacity: 1;
}

@media (max-width: 1024px) {
  .side-nav-page {
    flex-direction: column;
  }

  .side-nav-page .page-content {
    margin-left: 0;
  }
}

@media (max-width: 1024px), (max-height: 768px) {
  .ports-subscribe-modal {
    border-width: 2px;
  }
}

@media (max-width: 900px) {
  .ports-footer {
    flex-direction: column;
    height: auto;
    padding-top: 26px;
    padding-bottom: 26px;
  }

  .ports-footer > .copyright {
    margin-bottom: 15px;
    margin-right: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  .ports-footer > .links {
    margin-bottom: 15px;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-right: 0;
  }
}

@media (max-width: 768px) {
  .ports-side-bar .side-bar-children {
    width: 100%;
    max-width: 100%;
    padding-left: 30px;
    padding-right: 25px;
  }
}

@media (max-width: 768px), (max-height: 768px) {
  .ports-subscribe-modal {
    padding: 37px;
  }
}

@media screen and (max-width: 768px) {
  .ports-desktop-header {
    display: none;
  }

  .ports-mobile-header {
    display: flex;
  }
}

@media (max-width: 767px), (max-height: 767px) {
  .ports-subscribe-modal {
    border: none;
    width: 100%;
    height: 100%;
  }

  .ports-subscribe-modal .hide-btn {
    top: 17px;
    right: 17px;
  }

  .ports-subscribe-modal .page-border {
    display: flex;
  }

  .ports-subscribe-modal .page-margin {
    display: flex;
  }

  .ports-subscribe-modal.is-submitted .subscribe-form {
    display: none;
  }
}